
import BirdyService from '../BirdyService'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'

import Upload from './Upload'

export default {
  components: {
    Alert, Toast, Swiper, XButton, Box, Upload
  },
  data () {
    return {
      birdyService: new BirdyService(this),
      position: 'default',
      showTip: false,
      show: false,
      result: '贷款申请提交结果',
      errorTip: '请完善表单内容',
      content: {},
      target: {
        loan_amount: 0,
        income: 0,
        id_card_front: '',
        bank_pic: ''
      },
      demo06_index: 0,
      demo06_list: []
    }
  },
  created () {
    this.pageInit()
    let url = location.href.split('#')[0]
    this.birdyService.get(`/api/wechat/config?url=${url}`).then((resp) => {
      this.config = resp
      this.$wechat.config({
        debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
        appId: resp.appId, // 必填，公众号的唯一标识
        timestamp: resp.timestamp, // 必填，生成签名的时间戳
        nonceStr: resp.nonceStr, // 必填，生成签名的随机串
        signature: resp.signature,// 必填，签名，见附录1
        jsApiList: ['onMenuShareAppMessage'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
      })

      this.$wechat.onMenuShareAppMessage({
        title: '微信贷款在线申请', // 分享标题
        desc: '微信官方贷款在线申请', // 分享描述
        link: 'http://loan.bailongsheng.cn/webapp', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
        imgUrl: 'http://loan.bailongsheng.cn/upload/base/loan_logo.png', // 分享图标
        type: '', // 分享类型,music、video或link，不填默认为link
        dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
        success: function () {
            // 用户确认分享后执行的回调函数
        },
        cancel: function () {
            // 用户取消分享后执行的回调函数
        }
      })
    }).catch((resp) => {
      console.log(resp)
    }).finally(() => {

    })
  },
  methods: {
    demo06_onIndexChange (index) {
      this.demo06_index = index
    },
    pageInit () {
      this.birdyService.get(`/api/content/1?access-token=abc123_`).then((resp) => {
        this.content = resp.content
        this.demo06_list = resp.banners.map((item, index) => ({
          url: '',
          img: item.url,
          title: ''
        }))
      }).catch((resp) => {
        console.log(resp)
      }).finally(() => {

      })
    },
    apply () {
      if (!this.target.name) {
        this.showTip = true
        this.errorTip = "请填写您的真实姓名"
        return false
      }
      if (!this.target.tel) {
        this.showTip = true
        this.errorTip = "请填写您的手机号码"
        return false
      } else if (!(/^1[34578]\d{9}$/.test(this.target.tel))) {
        this.showTip = true
        this.errorTip = "请输入正确手机号码"
        return false
      }
      if (this.target.loan_amount == 0) {
        this.showTip = true
        this.errorTip = "请选择贷款金额"
        return false
      }
      if (!this.target.occupation) {
        this.showTip = true
        this.errorTip = "请填写您的职业"
        return false
      }
      if (this.target.income == 0) {
        this.showTip = true
        this.errorTip = "请选择您的家庭年收入"
        return false
      }
      if (!this.target.id_card_front) {
        this.showTip = true
        this.errorTip = "请上传身份证正面照片"
        return false
      }
      if (!this.target.bank_pic) {
        this.showTip = true
        this.errorTip = "请上传银行卡照片"
        return false
      }

      this.birdyService.create(`/api/loan/create?access-token=abc123_`).then((resp) => {
        // this.$router.push({ path: `/confirm/${resp.id}` })
        this.show = true
        this.target = {
          loan_amount: 0,
          income: 0,
          id_card_front: '',
          bank_pic: ''
        }
      }).catch((resp) => {
        alert(resp)
        console.log(resp)
      }).finally(() => {

      })
    }
  }
}



